export default {
    data() {
        return {
            currentTab: 'vcard'
        }
    },
    created() {
        this.getOrderList()
    },
    methods: {
        changeTabs (type) {
            this.currentTab = type
            this.getOrderList()
        },
        async getOrderList () {
            const params  = {
                page: 1,
                pageSize: 100,
                type: this.currentTab === 'venue' ? 0 : 1
            }
            const res = await this.$api.getOrderList(params)
            if (res.data.code === 0) {
                console.log(res)
            }
        }
    },
}
